a[link] {
  position: relative;
  display: inline-block;
  transition: all 400ms;
  margin-left: 0.1rem;
  margin-right: 0.1rem;
  color: #383838;
}

a[link]:hover {
  color: #366ebf;
}

a[link]:hover:after {
  border-bottom-style: solid;
  border-bottom-color: #366ebf;
}

a[link]:active {
  color: #20406f;
}

a[link]:active:after {
  border-bottom-style: dotted;
}

a[link]:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 0.125rem;
  bottom: 0;
  left: 0;
  border-bottom-width: 0.09375rem;
  border-bottom-style: dashed;
  border-bottom-color: #2b5797;
  transition: all 400ms;
}

